#magicContainer {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: -1;
	/* opacity: 0.2;  */
	opacity: 0;
	transition: opacity 1s ease-in-out; /* Add a smooth transition */
}

