.emoji-slider-container {
	width: 100%;
	padding: 1rem;
	user-select: none;
}

.emoji-slider-container.disabled {
	opacity: 0.5;
	cursor: not-allowed;
}

.emoji-display {
	text-align: center;
	margin-bottom: 1rem;
}

.current-emoji {
	font-size: 2.5rem;
	display: inline-block;
	transition: transform 0.2s ease;
}

.current-emoji:hover {
	transform: scale(1.1);
}

.slider-track {
	position: relative;
	height: 8px;
	background: rgba(255, 255, 255, 0.1);
	border-radius: 4px;
	cursor: pointer;
	margin: 1rem 0;
}

.slider-progress {
	position: absolute;
	height: 100%;
	background: linear-gradient(
		90deg,
		var(--color-primary),
		var(--color-secondary)
	);
	border-radius: 4px;
	transition: width 0.1s ease;
}

.slider-thumb {
	position: absolute;
	width: 20px;
	height: 20px;
	background: white;
	border-radius: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
	transition: transform 0.2s ease;
	cursor: grab;
}

.slider-thumb:hover {
	transform: translate(-50%, -50%) scale(1.1);
}

.slider-thumb:active {
	cursor: grabbing;
	transform: translate(-50%, -50%) scale(0.95);
}

.emoji-markers {
	display: flex;
	justify-content: space-between;
	margin-top: 0.5rem;
	padding: 0 0.5rem;
}

.marker {
	font-size: 1.25rem;
	opacity: 0.5;
	transition: all 0.2s ease;
	cursor: pointer;
}

.marker.active {
	opacity: 1;
	transform: scale(1.1);
}

.marker:hover {
	transform: scale(1.2);
}

html {
	box-sizing: border-box;
	-webkit-font-smoothing: antialiased;
}

* {
	box-sizing: inherit;
}

*:before,
*:after {
	box-sizing: inherit;
}

body {
	min-height: 100vh;
	display: flex;
	font-family: "Inter UI", Arial;
	justify-content: center;
	align-items: center;
	background: #f6f8ff;
}

body .dribbble {
	position: fixed;
	display: block;
	right: 20px;
	bottom: 20px;
}

body .dribbble img {
	display: block;
	height: 28px;
}

body .twitter {
	position: fixed;
	display: block;
	right: 64px;
	bottom: 14px;
}

body .twitter svg {
	width: 32px;
	height: 32px;
	fill: #1da1f2;
}
